export default "import { useMemo, useCallback, useState } from 'react';\nimport { Button, Table } from 'antd';\nimport { EditOutlined } from '@ant-design/icons';\nimport { useModal } from '@ebay/nice-modal-react';\nimport UserInfoModal from './UserInfoModal';\nimport mockData from './mock';\n\nexport default function UserList() {\n  const userModal = useModal(UserInfoModal);\n  const [users, setUsers] = useState(mockData);\n\n  const handleNewUser = useCallback(() => {\n    userModal.show().then((newUser) => {\n      setUsers([newUser, ...users]);\n    });\n  }, [userModal, users]);\n\n  const handleEditUser = useCallback(\n    (user) => {\n      userModal.show({ user }).then((newUser) => {\n        setUsers((users) => {\n          // Modify users immutablly\n          const i = users.findIndex((u) => u.id === newUser.id);\n          const updated = { ...users[i], ...newUser };\n          const arr = [...users];\n          arr.splice(i, 1, updated);\n          return arr;\n        });\n      });\n    },\n    [userModal],\n  );\n\n  const columns = useMemo(\n    () => [\n      {\n        title: 'Name',\n        dataIndex: 'name',\n        width: '150px',\n      },\n      {\n        title: 'Job Title',\n        dataIndex: 'job',\n      },\n      {\n        title: 'Edit',\n        width: '100px',\n        render(value, user) {\n          return (\n            <Button\n              type=\"link\"\n              icon={<EditOutlined />}\n              onClick={() => {\n                handleEditUser(user);\n              }}\n            />\n          );\n        },\n      },\n    ],\n    [handleEditUser],\n  );\n\n  return (\n    <div>\n      <Button type=\"primary\" onClick={handleNewUser}>\n        + New User\n      </Button>\n      <Table\n        size=\"small\"\n        rowKey=\"id\"\n        pagination={false}\n        columns={columns}\n        dataSource={users}\n        style={{ marginTop: '20px' }}\n      />\n    </div>\n  );\n}\n";